@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700|Playfair+Display:400,700&display=swap);
.header {
  padding: 2rem 1rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  font-family: 'Playfair Display', serif;
  overflow: hidden;
}

.header::after {
  content: '';
  display: block;
  clear: both;
}

.header .name {
  float: left;
  font-weight: bold;
}

.header .links {
  float: right;
}

.header .links a {
  padding: 0 2rem;
  color: #7E7E7E;
  position: relative;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.header .links a::after {
  content: '';
  display: inline-block;
  background: #171717;
  height: 2px;
  width: 0%;
  margin: auto;
  position: absolute;
  bottom: -5px;
  left: 50%;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.header .links a:hover {
  color: #171717;
}

.header .links a:hover::after {
  width: 60%;
  left: 20%;
}

.hamburger {
  display: none;
  cursor: pointer;
  float: right;
  font-size: 1.5rem;
}

@media (max-width: 600px) {
  .hamburger {
    display: block;
  }  
  .header .links {
    float: right;
    position: relative;
    top: 40px;
    left: 100%;
    -webkit-transition: 400ms;
    transition: 400ms;
  }
  .header .open-links {
    left: 5%;
    z-index: 4;
    background: #fff;
    padding: 1rem;
  }
  .header .links a {
    padding: 1rem 0;
    display: block;
    text-align: center;
  }
  .header .links a::after {
    bottom: 10px;
  }
  .header .links a:hover::after {
    width: 100%;
    left: 0%;
  }
}
.landing {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #171717;
}

.landing .frontend {
  position: relative;
  top: 20px;
  text-transform: uppercase;
}

.landing .webdev {
  font-family: 'Playfair Display', serif;
  font-size: 72px;
}

.landing .interests {
  color: #7E7E7E;
}

.landing .buttons {
  padding-top: 30px;
}

.landing .buttons a {
  padding: 1rem 2.5rem;
  -webkit-transition: 200ms;
  transition: 200ms;
  display: inline-block;
}

.landing .buttons a.portfolio {
  border: 3px solid #171717;
  background: #fff;
  color: #171717;
  margin-right: 10px;
}

.landing .buttons a.contact {
  border: 3px solid #171717;
  background: #171717;
  color: #fff;
  margin-left: 10px;
}

.landing .buttons a.portfolio:hover {
  background: #171717;
  color: #fff;
}

.landing .buttons a.contact:hover {
  background: #fff;
  color: #171717;
}

.landing .social-links {
  padding-top: 40px;
}

.landing .social-links a {
  margin: 0 1rem;
  padding: 0 0.5rem;
  font-size: 20px;
}

@media (max-width: 720px) {
  .landing .webdev {
    font-size: 52px;
  }
  .landing .buttons a {
    padding: 0.6rem 2rem;
  }
}

@media (max-width: 500px) {
  .landing .webdev {
    font-size: 42px;
  }
  .landing .buttons a {
    padding: 0.4rem 1.6rem;
    font-size: 14px;
  }
  .landing .frontend {
    top: 10px;
    font-size: 14px;
    text-transform: uppercase;
  }
  .landing .interests {
    font-size: 14px;
  }
  .landing .social-links a {
    margin: 0 0.8rem;
    padding: 0 0.4rem;
  }
}
.portfolio-heading {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center;
}

.portfolio-heading > h2 {
  font-family: "Playfair Display", Times Roman, serif;
  font-size: 3rem;
  font-weight: 700;
}

.load-more {
  text-align: center;
  margin-top: 2rem;
}

.load-more button {
  padding: 1rem 2.5rem;
  -webkit-transition: 200ms;
  transition: 200ms;
  display: inline-block;
  border: 3px solid #171717;
  background: #fff;
  color: #171717;
  cursor: pointer;
  outline: none;
}

.load-more button:hover {
  background: #171717;
  color: #fff;
  letter-spacing: 1px;
  box-shadow: 2px 2px 8px #171717;
}


