.project-card .title-wrapper {
  /* position: absolute; */
  z-index: 2;
  width: 100%;
  bottom: 0;
  padding: 1rem;
}

.project-card .title-wrapper .title {
  font-size: 1.87rem;
  font-family: "Playfair Display", Times Roman, serif;
  color: #151515;
  padding-bottom: 1rem;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.project-card:hover .title {
  padding-left: 1rem;
}

.project-card:hover .tech {
  color: #151515;
}

.project-card .tech {
