.footer {
  padding: 1.5rem 1rem;
  border-top: 1px solid #ccc;
  width: 100%;
  font-family: 'Playfair Display', serif;
  position: absolute;
  bottom: 0;
}

.footer::after {
  content: '';
  display: block;
  clear: both;
}

.footer .links {
  float: left;
}

.footer .social-links {
  float: right;
}

.footer .social-links a {
  color: #171717;
  padding: 0 1rem;
}

.footer .links a {
  padding: 0 1rem;
  color: #7E7E7E;
  position: relative;
  -webkit-transition: 200ms;
  transition: 200ms;
}

.footer .links a:hover {
  color: #171717;
}

@media (max-width: 600px) {
  .footer .links {
    float: none;
    text-align: center;
    padding-bottom: 20px;
  }
  
  .footer .social-links {
    float: none;
    text-align: center;
  }
}
.project-heading {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center;
}

.project-heading > h2 {
  font-family: "Playfair Display", Times Roman, serif;
  font-size: 3rem;
  font-weight: 700;
  position: relative;
  z-index: 2;
}

.project-details {
  width: 60%;
  float: left;
  padding-top: 5%;
  padding-right: 40px;
}

.project-image {
  width: 40%;
  margin-top: 2.5%;
  padding-bottom: 25%;
  float: right;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.project-details .description {
  font-size: 1.87rem;
  padding-bottom: 10px;
}

.project-details .tech {
  color: #666;
}

.project-details .buttons {
  padding-top: 30px;
}

.project-details .buttons a {
  padding: 0.5rem 1.5rem;
  -webkit-transition: 200ms;
  transition: 200ms;
  display: inline-block;;
}

.project-details .buttons a.project {
  border: 3px solid #171717;
  background: #fff;
  color: #171717;
  margin-right: 10px;
}

.project-details .buttons a.code {
  border: 3px solid #171717;
  background: #171717;
  color: #fff;
  margin-left: 10px;
}

.project-details .buttons a.project:hover {
  background: #171717;
  color: #fff;
}

.project-details .buttons a.code:hover {
  background: #fff;
  color: #171717;
}

@media (max-width: 800px) {
  .project-details {
    width: 100%;
    float: none;
  }
  .project-image {
    width: 100%;
    float: none;
    padding-bottom: 59%;
  }
  .project-details .description {
    font-size: 20px;
    padding-bottom: 10px;
  }
  .project-details .tech {
    font-size: 14px;
  }
  .project-details .buttons a {
    padding: 0.4rem 1.6rem;
    -webkit-transition: 200ms;
    transition: 200ms;
    display: inline-block;;
  }
}
.about {
  width: 50%;
}

.about-heading {
  padding-top: 100px;
  padding-bottom: 50px;
  text-align: center;
}

.about-heading > h2 {
  font-family: "Playfair Display", Times Roman, serif;
  font-size: 3rem;
  font-weight: 700;
}

.about-me {
  text-align: center;
  margin: auto;
  width: 75%;
  padding-bottom: 50px;
}

.skills > h2, .education > h2 {
  font-family: "Playfair Display", Times Roman, serif;
  padding-bottom: 1rem;
}

.skills {
  text-align: center;
}

.education {
  padding-top: 3rem;
  text-align: center;
}

.education .school {
  float: left;
  width: 33%;
  border-left: 1px solid #ccc;
}

.education .school .name {
  font-size: 1.2rem;
  padding-bottom: 8px;
}

.education .school .place {
  padding-bottom: 4px;
}

.education .school:nth-child(2) {
