  font-size: 3rem;
  font-weight: 700;
}

.contact-details {
  text-align: center;
}

.contact-details > h3 {
  font-family: "Playfair Display", Times Roman, serif;
  font-size: 2rem;
  padding: 8px 0;
}

.contact-details > p {
