}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

a {
  color: #171717;
  text-decoration: none;
}

.app-container {
  min-height: 100vh;
  position: relative;
  padding-bottom: 200px;
}

.container {
  margin: auto;
  max-width: 70%;
}

.container::after {
  content: '';
  display: block;
  clear: both;
}

.loading {
  padding: 100px 0;
  text-align: center;
  font-size: 3rem;
  font-family: 'Playfair Display', serif;
  font-weight: bold;
}

@media (max-width: 1400px) {
  .container {
    max-width: 80%;
  }
}

@media (max-width: 600px) {
  .container {
    max-width: 90%;
  }
}
